<template>
  <div class="search">
    <div>
      <van-nav-bar left-text="返回" left-arrow @click-left="goIndex()" @click-right="getSearch()">
        <template #title>
          <van-search shape="round" placeholder="请输入商品名称关键词" v-model="value" @search="getSearch()"></van-search>
        </template>
        <template #right>
          <van-button type="info">搜索</van-button>
        </template>
      </van-nav-bar>
    </div>

    <div class="menu-box">
      <div class="menu-nav">
        <div
          class="menu-list clearfix"
          v-for="(item,index) in searchData"
          :key="index"
          @click="goDetail(item.pid)"
        >
          <div class="menu-img fl">
            <img class="auto-img" :src="item.smallImg" alt />
          </div>
          <div class="menu-content fl clearfix">
            <div class="menu-name">{{item.name}}</div>
            <div class="menu-enname">{{item.enname}}</div>
            <div class="menu-desc one-text">简介:{{item.desc}}</div>
            <span class="menu-pri">￥{{item.price}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/search.less";
export default {
  name: "Search",
  data() {
    return {
      searchData: [],
      value: "",
    };
  },
  methods: {
    //搜索
    getSearch() {
     
      this.searchData = [];
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/search",
        params: {
          appkey: this.appkey,
          name: this.value,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "Q001") {
            this.searchData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    goIndex() {
      this.$router.push({ name: "Index" });
    },
    goDetail(pid) {
      this.$router.push({ name: "Detail", query: { pid: pid } });
    },
  },
};
</script>

<style lang="less" scoped>
</style>